<template>
  <div>
    <div class="comtent" :style="comtent_style" @mousedown="changebackground($event)" v-html="msg1">
  
    </div>
    <div class="open" @click="open" v-show="open_state">开</div>
    <div class="reset" @click="reset" v-show="reset_state">重置</div>
  </div>
  
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      speed: 200,
      msg1: '',
      msg2: '',
      open_state: 1,
      comtent_style: {
        background: '#fff',
        color: '#fff',
      },
    }
  },
  mounted () {
    
  },
  methods: {
    print (msg,msgs) {
     let len = 0
     let timeId=setInterval(() => {
				this[msg]= msgs.substring(0,len)+'_';
				if(len <= msgs.length){
          len++;
				} else{
				clearInterval(timeId)
				this[msg]=msgs.substring(0,len)
			 }
		  },this.speed);
    },
    del (msg,msgs) {
      let len = msgs.length
      let timer=setInterval(() => {
				this[msg]= msgs.substring(0,len)+'_';
				if(len >= msgs.length-12){
          len--;
				} else{
				clearInterval(timer)
				this[msg]=msgs.substring(0,len)
			 }
		  },this.speed);
    },
    open () {
      this.open_state = 0;
      let msgs = '嗨！很抱歉过了这么久才给你这个惊喜，不知道你喜不喜欢，或许我不该再来打扰你，让你烦心，但是我想完成这个作品，将它展示给你<br/>\
      这么久以来你一直陪伴这我，在我最艰难的时候，你鼓励我，安慰我，让我度过不快乐的日子,我要给你说一声谢谢'
      this.print('msg1',msgs)
      this.comtent_style.background = '#000'
    },
    reset () {
      this.comtent_style.background = "#000"
      this.comtent_style.color = "#fff"
    },
    changebackground (event) {
      let x = event.clientX;
      let _this = this
      document.onmousemove = function(event) {
        if (event.clientX - x > 100){
          document.onmouseup = function() {
            _this.comtent_style.background = "#" + (~~(Math.random() * (1 << 24))).toString(16);
            document.onmousemove = null;
          }
        }
        else if (event.clientX - x < -100) {
          document.onmouseup = function() {
            _this.comtent_style.color = "#" + (~~(Math.random() * (1 << 24))).toString(16);
            document.onmousemove = null;
          }
        }
      }
    }
  },
  computed: {
    reset_state () {
      return !this.open_state
    }
  }
}
</script>

<style scoped>
.comtent{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 0;
    word-break: break-all;
    box-sizing: border-box;
    font-size: 18px;
    transition: all 1s;
    padding: 8px;
    letter-spacing: 1px;
    line-height: 30px;
    user-select: none;
    text-indent: 20px;
  }
  .open{
    width: 80px;
    height: 80px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -40px 0 0 -40px;
    background: #000;
    border-radius: 40px;
    text-align: center;
    line-height: 80px;
    font-size: 30px;
  }
  .reset{
    width: 60px;
    height: 60px;
    position: fixed;
    right: 30px;
    bottom: 30px;
    background: #fff;
    color: #000;
    border-radius: 30px;
    text-align: center;
    line-height: 60px;
    font-size: 20px;
  }
</style>



